import React, {useRef} from 'react'
import { findDefaultOffering, findDefaultPricing, findPricingByName, SideScroll } from '../../PlanPurchase/util';

export default function BenefitList(props) {

  const ref = useRef(null);
  const getCurrSelection = (pind) => {
    //
    props.getCurrentInd(pind);
  }
  const HandleBack=()=>{
      SideScroll(ref.current,'left', 1, 100, 3)
  }

  const HandleNext=()=>{
      SideScroll(ref.current,'right', 1, 100, 3)
  }
  return (
    <>
      <div className="benfList_Dtls_Cls">
        <div className="bnfLstImgDvCls mobview">
          <img onClick={()=>HandleBack()} data-src="https://res.cloudinary.com/onsurity/image/upload/fl_lossy,f_auto,q_auto/v1593615228/New-home-screen/leftArrw_w7kedi.svg" alt="back" className="lazyload"/>
        </div>
        <div className="benfListOutScrlDiv" ref={ref}>
        {
          props.benfList.map((lobj,ind)=>(
            <div key={"bnfLKe"+ind} className={ind == props.currentInd ? "activeDtlCls benfListInnerDtlsCls" : "benfListInnerDtlsCls"} onClick={(e)=>getCurrSelection(ind)}>
              {lobj.label}
            </div>
          ))
        }
        </div>
        <div className="bnfLstImgDvCls mobview">
          <img onClick={()=>HandleNext()} data-src="https://res.cloudinary.com/onsurity/image/upload/fl_lossy,f_auto,q_auto/v1593615748/New-home-screen/rigthArrw_c9asvw.svg" alt="load" className="lazyload"/>
        </div>
      </div>
    </>
  )
}
