import React, {useState} from 'react'
import { findDefaultPricing, findPricingByName, SideScroll } from '../../PlanPurchase/util';

export default function TableData_Mob(props) {

  const[navPosition, setNavPositon]=useState(0);
  const[currPlan,setCurrPlan]=useState(0);

  const handleSlide=(index)=>{
    setCurrPlan(index);
      switch (index) {
          case 0:
              setNavPositon(0)
              // SideScroll(ref.current,'multi', 0, 200, 200)
              break;
          case 1:
              setNavPositon(110)
              break;
          case 2:
              setNavPositon(220)
              break;
          case 3:
              setNavPositon(330)
              break;
          case 4:
              setNavPositon(440)
              break;
          default:
              setNavPositon(0)
              break;
      }
  }

  const handleCardClicked=(data, index)=>{
    handleSlide(index)


  }
  return (
    <>
      <div className="main_nav_div">
          <div className="hightPlnTop px-3 blockmain_div">
            {props.bodyData.map((e, index)=>(
              <div className={currPlan == index? "price_block_mobile activeClsDtls" : "price_block_mobile"} onClick={()=>handleCardClicked(e, index)}>
                {e.Plan}
              </div>
            ))}
          </div>
          <div>
            <div style={{left:`${navPosition}px`}} className="mx-3 price_block_mobile_scroller"/>
          </div>
      </div>
      <div className="tableData_Dtls_Cls">
        <table className="tblDtlsCls">
          <thead className="theadDtlsCls">
            {
              props.headData.map((lname,ind)=>(
                <>{ind !== 0 ?
                  <th className="thDivClsInr">
                    <div className={"inrTblThDivCls thDivInrClsCnt"+ind}>
                      {lname}
                    </div>
                  </th>
                  : null}
                </>
              ))
            }
          </thead>
          <tbody>
            {
              props.bodyData.map((lobj,ind)=>(<>
                  {ind == currPlan ?
                <tr key={"trTbl"+ind}>
                  {false ?
                    <td className="planDtlsTdCls">
                      {lobj.Plan}
                    </td>
                  :null}
                  <td className="freCnstDtlsTdCls">
                    {lobj.freeConsultation}
                  </td>
                  <td className="AvailMmbrDtTdCls">
                    <div className="tdDivBldCls">
                      {lobj.label}
                    </div>
                    {
                      lobj.periodArr.map((lname,i)=>(
                        <div className="tdDivNrmlCls">
                          {lname}
                        </div>
                      ))
                    }
                  </td>
                </tr>
                :null}
              </>))
            }
          </tbody>
        </table>
      </div>
    </>
  )
}
