import React from 'react'

export default function SubHeadAndContnt(props) {

  return (
    <>
      <div className="benfDtls_Dtls_Cls ">
        <div className="DescrptnHeadDtlsCls subHeadMIDtlsCls">
          {props.data.label}
        </div>
        <div className="benfArrDtlsCls">
          {
            props.data.contentArr.map((ltext,ind)=>(
              <div key={"benfArr"+ind}
                className={props.data.style == "paragraph" ? "DescrptnCntDtlsCls subCntDtlsNw"
                  : props.data.style == "numbers" ? "DescrptnCntDtlsCls subCntDtlsNw inserNumB4Dtls"
                  : props.data.style == "dots" ? "DescrptnCntDtlsCls subCntDtlsNw inserDotsB4Dtls"
                  : ""
                }>
                {ltext}
              </div>
            ))
          }
        </div>
      </div>
    </>
  )
}
