import React, {useState} from 'react'

export default function FitnessReward(props) {

  return (
    <>
      <div className="descrptnSectnOut_Dtls_Cls">
        <div>
          {
            props.data.map((lname,ind)=>(
              <div key={"dtlsArr"+ind} className="DescrptnCntDtlsCls">
                {lname}
              </div>
            ))
          }
        </div>
      </div>
    </>
  )
}
