import React, {useState, useEffect} from 'react'
import './DetailsSkeleton.scss';
// import Payment from '../Payment/Components/Payment/Payment';
import { urls } from '../../routes/routes';
import {data} from './Details_data.js';

import PathDetails from './Components/PathDetails';
import BenefitList from './Components/BenefitList';
import TableData from './Components/TableData';
import DescriptionSection from './Components/DescriptionSection';

import FitnessReward from './Components/FitnessReward';
import DoctrConslt from './Components/DoctrConslt';
import SubHeadAndContnt from './Components/SubHeadAndContnt';

import TableData_Mob from './Components/TableData_Mob';
import Header from '../../components/Home/HeaderSection/Header';

export default function DetailsSkeleton(props) {

  useEffect(() => {
    if(props.location.state){
      const{index}=props.location.state;
      setCurrSelection(index)
    }
    // eslint-disable-next-line
  }, [])

    const [currentSelection, setCurrSelection] = useState(0);

    const getCurrInd = (pind) => {
      setCurrSelection(pind);
    }

    // const handleClick=()=>{
    //     props.history.push(urls.PAYMENT)
    // }

    return (
        <>
        <div style={{backgroundColor: '#FFF',width: '100%'}}>
            {/*<Header />*/}
             <div className="parentOut_Dtls_Cls">
              <PathDetails path={data.path} currentInsurance={data.featuresArr[currentSelection].label}/>
              <BenefitList benfList={data.featuresArr} getCurrentInd={getCurrInd} currentInd={currentSelection}/>

              {
                currentSelection == 0 ? (
                  <FitnessReward data={data.dataArr[currentSelection].labelParaTag}/>
                ) : currentSelection == 1 ? (
                  <>
                    <DoctrConslt data={data.dataArr[currentSelection].labelParaTag}/>
                    <div className="webviewDtlsPg">
                      <TableData headData={data.dataArr[currentSelection].head} bodyData={data.dataArr[currentSelection].body} />
                    </div>
                    <div className="mobViewDtlsPg">
                      <TableData_Mob headData={data.dataArr[currentSelection].head} bodyData={data.dataArr[currentSelection].body} />
                    </div>
                    <DescriptionSection DetailsHead=""
                      DetailsContentArr={data.dataArr[currentSelection].label} />

                    <DescriptionSection DetailsHead={data.dataArr[currentSelection].DetailsHead} DetailsContentArr={data.dataArr[currentSelection].DetailsContentArr}/>
                  </>
                ) : currentSelection == 2 ? (
                  <>
                    {data.dataArr[currentSelection].subHeadArr.map((lobj,ind)=>(
                      <DescriptionSection TableData={lobj.TableData} DetailsHead={lobj.DetailsHead} DetailsContentArr={lobj.DetailsContentArr}/>
                    ))}
                  </>
                ) : currentSelection == 3 ? (
                  <DescriptionSection DetailsHead="" DetailsContentArr={data.dataArr[currentSelection].labelParaTag}/>
                ) : currentSelection == 4 ? (
                  <div className="descrptnSectnOut_Dtls_Cls">

                  <DescriptionSection DetailsHead={data.dataArr[currentSelection].DetailsHead}
                    DetailsContentArr={data.dataArr[currentSelection].label} />
                    <DescriptionSection DetailsHead=""
                      DetailsContentArr={data.dataArr[currentSelection].contentArr} />

                    {/*<SubHeadAndContnt data={data.dataArr[currentSelection]}/>*/}
                  </div>
                ) : null
              }

              {/*<BenefitDetails txtDescription={data.txtDescription} benefitsArr={data.benefitsArr} />*/}

            </div>
        </div>
     </>
    )
}
