import React , { Suspense, useEffect }  from "react";
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
// import Loader from "../utils/Loader/Loader";
import CheckoutSkeleton from "../layouts/CheckOut/CheckoutSkeleton";
import PlanSkeleton from "../layouts/PlanPurchase/PlanSkeleton";
import PurchaseSkeleton from "../layouts/GetStarted/Skeleton";
import PhoneNumber from "../layouts/GetStarted/Components/Verification/PhoneNumber";
import Otp from "../layouts/GetStarted/Components/Verification/Otp";
import Gstin from "../layouts/GetStarted/Components/Gstin/Gstin";
import CompanyDetails from "../layouts/GetStarted/Components/CompanyDetails/CompanyDetails";
import PaymentSkeleton from "../layouts/Payment/PaymentSkeleton";
import SummarySkeleton from "../layouts/SummaryMob/SummarySkeleton";
import DetailsSkeleton from "../layouts/Details/DetailsSkeleton";
import ContactUsSkeleton from "../layouts/ContactUs/ContactUsSkeleton";
import {connect} from "react-redux";
import NotFound from "../Input-controls/404/NotFound";
import {heightWidthCalc} from '../utils/Api.js';
// import CoverageSkeleton from "../layouts/CovidCoverage/CoverageSkeleton";
import Faq from "../layouts/Faq/Faq";
// import BlogDesc from "../layouts/Blog-Desc/BlogDesc";
import Privacy from "../layouts/Privacy-Policy/Privacy";
import TermsOfUse from "../layouts/Terms-0f-Use/TermsOfUse";
import ScrollToTop from '../utils/ScrollToTop'
// import VerifyPayment from "../layouts/PaymentVerification/VerifyPayment";
import AboutUsSkeleton from "../layouts/About/AboutUsSkeleton";
import Success from "../layouts/PaymentSuccess/Success";
import Failed from "../layouts/PaymentFail/Failed";
import Pending from "../layouts/Payment-Pending/Pending";
import CareerSkeleton from '../layouts/Career/Job-Listing/CareerSkeleton';

import Home from '../layouts/Home/Home';
import HomeLetsVenture from '../layouts/HomeLetsVenture/Home';
import LGHome from '../layouts/HomeLG/Home'
import AddPeople from '../layouts/AddPeople/AddPeople_Skelton'
import {OnsurityUrls} from './urls'
import Coverage from "../layouts/Coverage/Coverage";
import FitnessRewards_Details from "../layouts/Benefits-Details/FitnessRewards_Details";
import DiscountMedicine_Details from "../layouts/Benefits-Details/DiscountMedicine_Details";
import HealthCheckup_Details from "../layouts/Benefits-Details/HealthCheckup_Details";
import DoctorConsult_Details from "../layouts/Benefits-Details/DoctorConsult_Details";
import GroupHealthins_Details from "../layouts/Benefits-Details/GroupHealthins_Details";
import GoodDoctor_Details from "../layouts/Benefits-Details/GoodDoctor_Details";
import { GroupTemLife_Data } from "../layouts/Benefits-Details/Data/GroupTerm_life_Data";
import GroupTerm_life_Details from "../layouts/Benefits-Details/GroupTerm_life_Details";

import MarketingPage from "../layouts/Marketing/MarketingPage";
import Oops from "../layouts/Oops-NotFound/Oops";
import AppRedirect from "../layouts/AppRedirect/AppRedirect";

// const Home = React.lazy(() => import('../layouts/Home/Home'));
// const AddPeople = React.lazy(() => import('../layouts/AddPeople/AddPeople_Skelton'));


//this export is used everywhere dont delete it !!!
export const urls=OnsurityUrls;

const Routes =(props)=>{


  const GetHomeUrl=()=>{
    if("LG" in localStorage && localStorage.getItem("LG")==1){
      return <Route exact path={urls.HOME} component={LGHome} />
    }else{
      return <Route exact path={urls.HOME} component={Home} />
    }
  }

  return(
    <Router>
      {/* <Suspense fallback={<Loader/>}> */}
        {/* THIS WILL SCROLL THE COMPONENT TO THE TOP EVERYTIME ROUTE CHANGES */}
        <ScrollToTop/>
        <Switch>
          {GetHomeUrl()}
            {/* <Route exact path={urls.LGHOME} component={LGHome} /> */}
            <Route path={urls.BLOG_DESC} component={()=>window.location.href="https://www.onsurity.com/discover/?t=1595570146590"} />
            
            <Route path={urls.PRIVACY_POLICY} component={Privacy} />
            <Route path={urls.PRIVACY_POLICY_APP} component={Privacy} />

            <Route path={urls.LETS_VENTURE} component={HomeLetsVenture} />
            <Route path={urls.MATRIX_PARTNER} component={HomeLetsVenture} />
            <Route path={urls.LIGHT_SPEED_VENTURE} component={HomeLetsVenture} />
            <Route path={urls.CHIRATE_VENTURE} component={HomeLetsVenture} />
            <Route path={urls.NEXUS_VENTURE} component={HomeLetsVenture} />
            <Route path={urls.ANGELIST} component={HomeLetsVenture} />
            <Route path={urls.FLOURISH_VENTURE} component={HomeLetsVenture} />
            <Route path={urls.ACCION} component={HomeLetsVenture} />
            <Route path={urls.OMIDYAR_NETWORK} component={HomeLetsVenture} />
            <Route path={urls.MAYFIELD_VENTURE} component={HomeLetsVenture} />
            <Route path={urls.INDIAN_NETWORK} component={HomeLetsVenture} />
            <Route path={urls.MUMBAI_NETWORK} component={HomeLetsVenture} />
            <Route path={urls.KAE_CAPITAL} component={HomeLetsVenture} />
            <Route path={urls.STARTUP} component={HomeLetsVenture} />

            <Route path={urls.Terms_USE} component={TermsOfUse} />
            <Route path={urls.SELECT_PLAN} component={PlanSkeleton} />
            <Route path={urls.ADD_PEOPLE} component={AddPeople} />
            <Route path={urls.CHECKOUT} component={CheckoutSkeleton} />
            <Route path={urls.SUMMARY_MOB} component={SummarySkeleton} />
            <Route path={urls.PAYMENT} component={PaymentSkeleton} />
            <Route path={urls.DETAILS} component={DetailsSkeleton} />
            <Route path={urls.COVERAGES} component={Coverage} />
            <Route path={urls.FAQ} component={()=>window.location.href=" https://onsurity.com/faqs/"}/>
            
            <Route path={urls.CONTACT_US} component={ContactUsSkeleton} />
            <Route path={urls.CONTACT_US_APP} component={ContactUsSkeleton} />

            <Route path={urls.PAYMENT_VERIFICATION} component={Success} />
            {/* VerifyPayment */}
            <Route path={urls.ABOUT_US} component={AboutUsSkeleton} />
            <Route path={urls.ABOUT_US_MOBILE} component={AboutUsSkeleton} />
            {/* <Route path={urls.PAYMENT_SUCCESS} component={Success} /> */}
             <Route path={urls.PAYMENT_FAIL} component={Failed} />
             <Route path={urls.PAYMENT_PENDING} component={Pending} />
             <Route path={urls.OOPS_NOTFOUND} component={Oops} />

             <Route path={urls.APP} component={AppRedirect} />



             <Route path={urls.FITNESS_REWARDS} component={FitnessRewards_Details} />
             <Route path={urls.DISCOUNT_MEDICINE} component={DiscountMedicine_Details}/>
             <Route path={urls.HEALTH_CHECKUP} component={HealthCheckup_Details}/>
             <Route path={urls.DOCTOR_CONUSLTATION} component={DoctorConsult_Details}/>
             <Route path={urls.GROUP_HEALTH} component={GroupHealthins_Details}/>
             <Route path={urls.GOOD_DOCTOR} component={GoodDoctor_Details}/>
             <Route path={urls.TERM_LIFE} component={GroupTerm_life_Details}/>

             <Route path={urls.PRODUCT_PAGE} component={MarketingPage}/>

             <Route path={urls.TERMS_OF_USE_MOB} component={TermsOfUse} />
             <Route path={urls.PRIVACY_POLICY_MOB} component={Privacy} />

             {/* <Route path={urls.CAREER} component={CareerSkeleton} />
             <Route path={urls.JOB_DESCRIPTION} component={Desc} />
             <Route path={urls.JOB_APPLY} component={ApplyForm} />
             <Route path={urls.UPLOAD} component={Upload} /> */}
            <Route path={urls.NOTFOUND} component={NotFound}/>

            {
              heightWidthCalc().width < 800 ? (
                <Route path={urls.SUMMARY_MOB} component={SummarySkeleton}/>
              ) : null
            }

            <PurchaseSkeleton HandleClick={props.HandleClick}>
              <Route path={urls.VERIFY_PHONE} component={PhoneNumber}/>
              <Route path={urls.VERIFY_OTP} component={Otp}/>
              <Route path={urls.GSTIN_DETAILS} component={Gstin}/>
              <Route path={urls.COMPANY_DETAILS} component={CompanyDetails}/>
            </PurchaseSkeleton>

        </Switch>
      {/* </Suspense> */}
    </Router>
  )
}


const mapStateToProps = (state) => ({
  auth:state.navigationStack.authObj
})



export default connect(mapStateToProps, null)(Routes)
