import React from 'react';
import SubHeadAndContnt from './SubHeadAndContnt';
import TableData_MI_Web from './TableData_MI_Web';

export default function DescriptionSection(props) {

  const checkTypeObj = (ptypeCheck) => {
    
    if(typeof(ptypeCheck) == "string")
    {
      return "string";
    }
    else if(typeof(ptypeCheck) == "object"){
      return "object";
    }
  }

  return (
    <>
      <div className="descrptnSectnOut_Dtls_Cls">
        <div className="DescrptnHeadDtlsCls">
          {props.DetailsHead}
        </div>
        <div>
          {props.DetailsContentArr.length > 0 ? (
              <>
              {
                props.DetailsContentArr.map((lname,ind)=>(
                <>
                  {checkTypeObj(lname) == "string" ? (
                    <div key={"dtlsArr"+ind} className="DescrptnCntDtlsCls">
                      {lname}
                    </div>
                  )
                  : checkTypeObj(lname) == "object" ? (
                    <SubHeadAndContnt data={lname}/>)
                  : null }
                  {
                    props.TableData !== undefined ? (
                      <TableData_MI_Web data={props.TableData} />
                    ) : null
                  }
                </>
              ))
              }
              </>
            ) : null
          }
        </div>
      </div>
    </>
  )
}
