import React from 'react'
import {urls} from '../../../../routes/routes'

export default function OrganizationBenefits(props) {

    const handleCick=()=>{
        if('url' in props.data && props.data.url){
            window.open(urls[props.data.url], "_open")
        }
    }

    return (
        <div className="mb-3">
             <div onClick={()=>handleCick()} className={`OrgDynamicCards ${props.index!=0 ? "mx-4":"mr-4"} ${props.classNm}`}  style={{backgroundColor:props.mainData.cardBackground, minHeight:props.mainData.noDesc && "290px"}}>
                <div className="mb-3 mt-2 mx-1 text-center">
                    <img height="180px" alt={props.data.Alt} data-src={props.data.Image} className="lazyload"/>
                </div>
                <div className="CardDymcHeading text-center py-1 px-4 cntHead_sn">
                    {props.data.Heading}
                </div>
                <div className="text-center px-3 cnt_subH_sn">
                    {props.data.SubHeading} 
                </div>
            </div>
        </div>
    )
}
 