import React, {useState} from 'react'

export default function TableData_MI_Web(props) {

  return (
    <>
      <div className="tableData_Dtls_Cls">
        <table className="tblDtlsCls">
          <thead className="theadDtlsCls">
            {
              props.data.head.map((lname,ind)=>(
                <>
                  <th className="thDivClsInr">
                    <div className={"inrTblThDivCls thDivInrClsCnt"+ind}>
                      {lname}
                    </div>
                  </th>
                </>
              ))
            }
          </thead>
          <tbody>
            {
              props.data.body.map((lobj,ind)=>(
                <tr key={"trTbl"+ind}>
                  <td className="planDtlsTdCls">
                    {lobj.col1}
                  </td>
                  <td className="AvailMmbrDtTdCls">
                    <div className="tdDivBldCls">
                      {lobj.col2.label}
                    </div>
                    {
                      lobj.col2.contentArr.map((lname,i)=>(<>
                        {lname != "" ?
                          <div className={"tdDivNrmlCls"}>
                            {lname}
                          </div>
                        : null}
                      </>))
                    }
                  </td>
                </tr>
              ))
            }
          </tbody>
        </table>
      </div>
    </>
  )
}
