import React, {useState} from 'react'

export default function TableData(props) {

  return (
    <>
      <div className="tableData_Dtls_Cls">
        <table className="tblDtlsCls">
          <thead className="theadDtlsCls">
            {
              props.headData.map((lname,ind)=>(
                <>
                  <th className="thDivClsInr">
                    <div className={"inrTblThDivCls thDivInrClsCnt"+ind}>
                      {lname}
                    </div>
                  </th>
                </>
              ))
            }
          </thead>
          <tbody>
            {
              props.bodyData.map((lobj,ind)=>(
                <tr key={"trTbl"+ind}>
                  <td className="planDtlsTdCls">
                    {lobj.Plan}
                  </td>
                  <td className="freCnstDtlsTdCls">
                    {lobj.freeConsultation}
                  </td>
                  <td className="AvailMmbrDtTdCls">
                    <div className="tdDivBldCls">
                      {lobj.label}
                    </div>
                    {
                      lobj.periodArr.map((lname,i)=>(
                        <div className="tdDivNrmlCls">
                          {lname}
                        </div>
                      ))
                    }
                  </td>
                </tr>
              ))
            }
          </tbody>
        </table>
      </div>
    </>
  )
}
